




















import { Component, Vue, Prop } from 'vue-property-decorator'
import Page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import { EventBus } from '../../until/event-bus'
@Component({
  components: {
    Page,
    pageView
  }
})
export default class carInfo extends Vue {
  $createDialog : any
  $createToast : any
  carList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  carId : any = ''
  carNums = 0
  carInfo : any = {}
  created () {
    this.carId = this.$route.query.carId
    this.getCarInfo({})
  }
  mounted () {
    EventBus.$on('carInfoChange', (info: any) => {
      console.log(info, '&&&&&&&&&&&&&&&&&&&&&&&&&&&&&')
      this.carInfo = info
    })
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  getCarInfo (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let carId = this.carId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getCarInfo', { carId: carId }).then((response) => {
      if (response.success) {
        this.carNums = response.data.nums
        this.carInfo = response.data.carInfo
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getCarInfo({ skip: skip })
  }
  goTo (type : any) {
    this.$router.push({ name: type, query: { carId: this.carId, fleetId: this.fleetId, carInfo: this.carInfo } })
  }
  sendSMS () {
    console.log(this.carInfo)
    let sendTime = this.carInfo.departureTime.split(' ')[0]
    let licensePlate = this.carInfo.licensePlate
    if (!licensePlate) {
      const toast = this['$createToast']({
        time: 2000,
        txt: '车牌号未填写',
        type: 'none'
      })
      toast.show()
      return
    }
    let content = '【么么校园】高校大巴，将在' + sendTime + '发车，车牌号：' + licensePlate + '，乘车地请到公众号查看。有任何问题请及时联系我们。<p style="color:red">注意：每辆车短信只能发一次，请确认好再发送。</p>'
    let dialog = this.$createDialog({
      type: 'confirm',
      title: '短信确认',
      content: content,
      confirmBtn: {
        text: '确认发送',
        active: true,
        disabled: false,
        href: 'javascript:;'
      },
      cancelBtn: {
        text: '取消',
        active: false,
        disabled: false,
        href: 'javascript:;'
      },
      onConfirm: () => {
        const toast = this['$createToast']({
          txt: '发送中...',
          type: 'none'
        })
        toast.show()
        this.$store.dispatch('sendSMS', { carInfo: this.carInfo }).then((response) => {
          if (response.success) {
            toast.hide()
            const toastSuc = this['$createToast']({
              txt: '发送成功',
              type: 'none',
              time: 3000
            })
            toastSuc.show()
          } else {
            const toast = this['$createToast']({
              txt: '发送失败',
              type: 'none',
              time: 3000
            })
            toast.show()
          }
        })
      },
      onCancel: () => {}
    })
    dialog.show()
  }
}
